body {
  font-family: 'calibri', sans-serif;
  font-weight: 600;
  background-color: rgba(128, 128, 128, 0.3);
}

h1 {
  font-style: italic;
}

.set-image {
  width: 50px;
  height: 50px;
}

.mtg-logo {
  width: 278px;
  height: 94.5px;
}

.doomguy {
  width: 94.5px;
  height: 94.5px;
}

.set-container {
  display: flex;
  align-items: center;
}

.guess-form-container {
  display: flex;
  width: 1000px;
  max-width: 85%;
  align-items: center;
  justify-content: space-between;
  margin: auto;
}

.guess-form-wrapper {
  position: relative;
}

.guess-form-input {
  position: relative;
  font-size: 30px;
  padding: 5px;
}

.guess-form-ul {
  max-height: 400px;
  overflow: auto;
  width: 300px;
  position: absolute;
  background-color: white;
  margin: 0px;
  list-style-type: none;
}

.guess-form-li {
  width: 100%;
  border-top: 1px solid lightgray;
}

.active-guess {
  border-top: 1px solid lightgray;
  background-color: lightblue;
}

.guess-form-li:hover {
  border-top: 1px solid lightgray;
  background-color: lightblue;
}

.correct {
  background-color: rgba(50,205,50, 0.5);
}

.correct-almost {
  background-color: rgba(255, 255, 0, 0.5);
}

table {
  border-collapse: collapse;
  width: 1000px;
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.guess-table td {
  border: 1px solid;
  padding: 5px;
  text-align: center;
  min-width: 100%;
  display: table-cell;
  /* overflow: hidden;
  text-overflow: ellipsis;
  width: 100%; */
}

.indicator-bar {
  background: linear-gradient(0deg, red);
}

.table-set-centered {
  margin: auto;
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.guess-table th {
  text-align: center;
  /* white-space: nowrap; */
  /* text-transform: uppercase; */
}

#set-column {
  width: auto;
  justify-items: left;
}

.table-set-image {
  height: 30px;
  width: 30px;
  margin-right: 1px;
}

@keyframes winner {
  0% { color: blue; }
  50% { color: green; }
  100% { color: blue; }
}

.winner {
  animation-name: winner;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}

@keyframes loser {
  0% { color: red; }
  50% { color: orange; }
  100% { color: red; }
}

.loser {
  animation-name: loser;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}